import React, { useState, useEffect } from 'react';
import './WorkflowView.css';
import Timeline from 'react-calendar-timeline';
import 'react-calendar-timeline/lib/Timeline.css';
import moment from 'moment';
import { useNavigate } from 'react-router-dom'; // Use useNavigate from react-router-dom

function WorkflowView() {
 const [workflowData, setWorkflowData] = useState([]);
 const [loading, setLoading] = useState(false);
 const [error, setError] = useState(null);

 // State for dynamic filter values
 const [workflows, setWorkflows] = useState([]);
 const [steps, setSteps] = useState([]);
 const [documents, setDocuments] = useState([]);

 const [selectedWorkflow, setSelectedWorkflow] = useState('all');
 const [selectedStep, setSelectedStep] = useState('all');
 const [selectedDocument, setSelectedDocument] = useState('');
 const [fi0, setFi0] = useState(2024);

 const navigate = useNavigate(); // React router navigate function

 // Fetch workflows, steps, and documents as before
 useEffect(() => {
   fetch(`/api/get_workflows`)
     .then((response) => response.json())
     .then((data) => {
       if (Array.isArray(data.workflows)) {
         setWorkflows([{ id: 'all', name: 'All Workflows' }, ...data.workflows]);
       } else {
         setWorkflows([{ id: 'all', name: 'All Workflows' }]);
       }
     })
     .catch((error) => setError(error));
 }, []);

 useEffect(() => {
   if (selectedWorkflow === 'all') {
     fetch(`/api/get_steps`)
       .then((response) => response.json())
       .then((data) => {
         if (Array.isArray(data.steps)) {
           setSteps([{ id: 'all', name: 'All Steps' }, ...data.steps]);
         } else {
           setSteps([{ id: 'all', name: 'All Steps' }]);
         }
       })
       .catch((error) => setError(error));
   } else {
     fetch(`/api/get_steps?workflow_id=${selectedWorkflow}`)
       .then((response) => response.json())
       .then((data) => {
         if (Array.isArray(data.steps)) {
           setSteps([{ id: 'all', name: 'All Steps' }, ...data.steps]);
         } else {
           setSteps([{ id: 'all', name: 'All Steps' }]);
         }
       })
       .catch((error) => setError(error));
   }
 }, [selectedWorkflow]);

 useEffect(() => {
   let queryString = `/api/documents?workflow_id=${selectedWorkflow}&step_id=${selectedStep}&fi0=${fi0}`;
   if (selectedDocument !== '') {
     queryString += `&id=${selectedDocument}`;
   }

   setLoading(true);

   fetch(queryString)
     .then((response) => response.json())
     .then((data) => {
       console.log('Fetched Data:', data);
       if (Array.isArray(data)) {
         setWorkflowData(data);
         setDocuments(data);
       } else {
         setWorkflowData([]);
         setDocuments([]);
       }
       setLoading(false);
     })
     .catch((error) => {
       setError(error);
       setLoading(false);
     });
 }, [selectedWorkflow, selectedStep, fi0, selectedDocument]);

 const handleSubmit = (e) => {
   e.preventDefault();
   setError(null);
   setLoading(true);

   let queryString = `/api/documents?workflow_id=${selectedWorkflow}&step_id=${selectedStep}&fi0=${fi0}`;
   if (selectedDocument !== '') {
     queryString += `&id=${selectedDocument}`;
   }

   fetch(queryString)
     .then((response) => response.json())
     .then((data) => {
       if (Array.isArray(data)) {
         setWorkflowData(data);
         setDocuments(data);
       } else {
         setWorkflowData([]);
         setDocuments([]);
       }
       setLoading(false);
     })
     .catch((error) => {
       setError(error);
       setLoading(false);
     });
 };

 // Prepare groups and items for the timeline
 const groups = workflowData.map((doc) => ({
   id: doc.id,
   title: `Doc: ${doc.name}`,
 }));

 const items = workflowData.map((doc) => ({
   id: doc.id,
   group: doc.id,
   title: `Doc: ${doc.name}`,
   start_time: moment(doc.workflows[0]?.date_start),
   end_time: moment(doc.workflows[0]?.date_end),
 }));

 // Handle item click to navigate to the edit page
 const handleItemClick = (itemId) => {
   navigate(`/open_admin_3/document_workflow/edit/?id=${itemId}&url=%2Fopen_admin_3%2Fdocument_workflow%2F`);
 };

 return (
   <div className="container">
     <h1 className="title">Workflow Data</h1>
     <form onSubmit={handleSubmit} className="form-container">
       <div className="form-group">
         <label htmlFor="workflow-select">Workflow:</label>
         <select
           id="workflow-select"
           value={selectedWorkflow}
           onChange={(e) => setSelectedWorkflow(e.target.value)}
           className="form-control"
         >
           <option value="all">All Workflows</option>
           {workflows.map((wf) => (
             <option key={wf.id} value={wf.id}>
               {wf.name}
             </option>
           ))}
         </select>
       </div>

       <div className="form-group">
         <label htmlFor="step-select">Step:</label>
         <select
           id="step-select"
           value={selectedStep}
           onChange={(e) => setSelectedStep(e.target.value)}
           className="form-control"
         >
           <option value="all">All Steps</option>
           {steps.map((step) => (
             <option key={step.id} value={step.id}>
               {step.name}
             </option>
           ))}
         </select>
       </div>

       <div className="form-group">
         <label htmlFor="year-select">Year (FI0):</label>
         <select
           id="year-select"
           value={fi0}
           onChange={(e) => setFi0(e.target.value)}
           className="form-control"
         >
           <option value="2024">2024</option>
           <option value="2023">2023</option>
           <option value="2022">2022</option>
           <option value="2021">2021</option>
           <option value="2020">2020</option>
         </select>
       </div>

       <div className="form-group">
         <label htmlFor="document-select">Document:</label>
         <select
           id="document-select"
           value={selectedDocument}
           onChange={(e) => setSelectedDocument(e.target.value)}
           className="form-control"
         >
           <option value="">All Documents</option>
           {documents.map((doc) => (
             <option key={doc.id} value={doc.id}>
               {doc.name}
             </option>
           ))}
         </select>
       </div>

       <button type="submit" className="btn btn-primary">
         Fetch Workflow
       </button>
     </form>

     {loading && <div>Loading...</div>}
     {error && <div className="error">Error: {error.message}</div>}

     {!loading && workflowData.length > 0 && (
       <Timeline
         groups={groups}
         items={items}
         defaultTimeStart={moment().startOf('year')}
         defaultTimeEnd={moment().endOf('year')}
         // Temporarily comment this out for debugging
         // onItemClick={handleItemClick}
       />
     )}
   </div>
 );
}

export default WorkflowView;
